import React from "react";
import { motion } from "framer-motion";
import { Link } from 'react-router-dom';

/**
 * Navigation Links component that displays main navigation sections as interactive cards
 * @param {Object} props - Component props
 * @param {Function} props.scrollToProjects - Function to scroll to projects section
 * @returns {React.Component} Links component
 */
const Links = ({ scrollToProjects }) => {
  const linkCardStyle = "select-none bg-gray-300 py-6 px-4 rounded-lg shadow-md hover:shadow-xl transition-shadow duration-300";
  const headingStyle = "text-2xl font-bold mb-2 text-center text-gray-800";
  
  return (
    <div className="mx-auto max-w-4xl w-[80%] grid grid-cols-1 gap-8 lg:grid-cols-2 my-12">
      {/* Projects Section */}
      <button 
        onClick={scrollToProjects}
        className="focus:outline-none"
        aria-label="View projects"
      >
        <motion.section
          whileTap={{ scale: 0.9 }}
          whileHover={{ scale: 1.05 }}
          className={linkCardStyle}
        >
          <h2 className={headingStyle}>Projects</h2>
        </motion.section>
      </button>

      {/* Previous Jobs Section */}
      <Link 
        to="/prejobs"
        className="focus:outline-none"
        aria-label="View previous experience"
      >
        <motion.section
          whileTap={{ scale: 0.9 }}
          whileHover={{ scale: 1.05 }}
          className={linkCardStyle}
        >
          <h2 className={headingStyle}>Previous Experience</h2>
        </motion.section>
      </Link>
    </div>
  );
};

export default Links;