
import React, { useEffect } from 'react';
import { Route, Routes } from 'react-router-dom';
import ReactGA from "react-ga";

import NavBar from './Components/NavBar';
import Home from './Home';
import Prejobs from './previous-jobs';

const TRACKING_ID = "G-XPBN89JV68";

function App() {
  useEffect(() => {
    ReactGA.initialize(TRACKING_ID);
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, []);

  const openSiteLockVerification = (e) => {
    e.preventDefault();
    window.open(
      'https://www.sitelock.com/verify.php?site=trentblacksportfolio.com',
      'SiteLock',
      'width=600,height=600,left=160,top=170'
    );
  };

  return (
    <main className='bg-gray-700 min-h-screen'>
      <NavBar className="NavBar" />
      <div className="container mx-auto">
        <Routes>
          <Route path='/' element={<Home />} />
          <Route path='/prejobs' element={<Prejobs />} />
        </Routes>
        
        <div className="text-center py-4">
          <a 
            href="#" 
            onClick={openSiteLockVerification}
          >
            <img 
              className="inline-block" 
              alt="SiteLock" 
              title="SiteLock" 
              src="https://shield.sitelock.com/shield/trentblacksportfolio.com" 
            />
          </a>
        </div>
      </div>
    </main>
  );
}

export default App;
