import React, { useRef } from 'react';
import { motion } from "framer-motion";

import Me from './assets/Me.jpg';
import Projects from './Components/Projects';
import Background from './Components/background';
import Links from './Components/Links';

const Home = () => {
  const projectsSectionRef = useRef(null);

  const scrollToProjects = () => {
    projectsSectionRef.current.scrollIntoView({ behavior: 'smooth' });
  };
  
  return (
    <div className="mt-16 pb-8">
      {/* Hero Section */}
      <section className="py-10">
        <div className="text-center p-6 md:p-10">
          <h2 
            id="HOME" 
            className="text-5xl pt-2 text-teal-600 font-medium md:text-6xl select-none"
          >
            Trent Black
          </h2>
          <h3 
            className="text-2xl pt-2 md:text-3xl text-white select-none"
          >
            Computer Engineer
          </h3>
        </div>
      
        <motion.div 
          className="mx-auto w-80 md:w-[20%] bg-gradient-to-t from-gray-900 to-teal-600 rounded-full"
          whileTap={{ scale: 0.85 }}
          whileHover={{ scale: 1.2 }}
        >
          <img 
            alt="Profile picture" 
            draggable="false" 
            className="rounded-3xl mx-auto w-48 md:h-[100%]" 
            src={Me} 
          />
        </motion.div>
      </section>
     
      {/* Project Section */}
      <Projects id="projects" ref={projectsSectionRef} />
      
      {/* Background Section */}
      <Background />
      
      {/* Links Section */}
      <Links scrollToProjects={scrollToProjects} />
    </div>
  );
};

export default Home;