const Projects = React.forwardRef((props, ref) => {
  const [topic, setTopic] = useState("All");
  const [projects, setProjects] = useState(icons);

  useEffect(() => {
    ReactGA.pageview(window.location.pathname);
  }, []);

  const filterProjects = (selectedTopic) => {
    setTopic(selectedTopic);
    setProjects(
      selectedTopic === "All"
        ? icons
        : icons.filter((p) => p.topic === selectedTopic)
    );
  };

  return (
    <section id="projects" ref={ref}>
      <div className="sm:py-0 py-10">
        <motion.div
          whileTap={{ scale: 0.95 }}
          whileHover={{ scale: 1.2 }}
          className="text-center mx-auto w-[50%] md:w-[15%] lg:w-[15%] h-11 bg-gradient-to-t from-gray-900 to-teal-600 rounded-full"
        >
          <h3
            className="font-BebasNeue sm:text-2xl text-3xl text-gray-100 select-nonep-1"
            id="PROJECT"
          >
            Projects
          </h3>
        </motion.div>

        <div className="flex flex-col mx-auto sm:my-5 mt-10">
          <div className="flex flex-row mx-auto p-2">
            <h4 className="font-BebasNeue text-3xl text-teal-600 select-none p-1">
              Filter
            </h4>
            <h4 className="font-BebasNeue text-3xl text-teal-600 select-none p-1">
              Topic:
            </h4>
            <h4 className="font-BebasNeue text-3xl text-teal-500 select-none p-1">
              {topic}
            </h4>
          </div>

          <div className="mx-auto flex xsm:flex-col sm:flex-row mt-5 gap-2">
            {["All", "Python", "Micro Processors", "FPGA", "Other"].map((t) => (
              <button
                key={t}
                onClick={() => filterProjects(t)}
                className="bg-teal-600 md:mx-5 md:mt-5 mt-1 hover:bg-white hover:text-teal-600 text-white font-bold md:py-2 px-4 rounded-full py-1 text-xs md:text-xl"
              >
                {t}
              </button>
            ))}
          </div>
        </div>
      </div>

      <h1 className="sm:text-lg text-center text-3xl text-gray-200 bg-teal-800 sm:mx-4 mx-80 sm:my-0 my-5 sm:px-3 p-4">
        ALL PROJECTS CAN BE CLICKED AND HAVE A VIDEO
      </h1>

      <motion.div className="flex flex-row flex-wrap gap-10 pb-5 mb-20 rounded-full">
        {projects.map((Project) => (
          <div
            key={Project.key}
            className="mx-auto lg:basis-1/5 basis-1/4 flex-2 my-5"
          >
            <Modal
              name={Project.title}
              img={Project.img}
              des={Project.des}
              vid={Project.vid}
              pic={Project.pic}
            >
              <motion.img
                loading="lazy"
                whileTap={{ scale: 0.6 }}
                draggable="false"
                className="aspect-square rounded-full object-cover mx-auto overflow-hidden w-[90%] h-[90%] md:w-[80%] md:h-[80%] lg:w-[60%] lg:h-[60%] cursor-pointer transition-all duration-300 ease-in-out"
                layout="responsive"
                src={Project.img}
                whileHover={{
                  scale: 1.2,
                  boxShadow: "0px 10px 50px rgb(0,0,0)",
                }}
              />
              <motion.h1
                whileTap={{ scale: 0.9 }}
                whileHover={{ scale: 1.3 }}
                className="text-l text-center md:py-1 lg:py-5 md:text-2xl lg:text-2xl text-gray-100 cursor-pointer transition-all duration-300 ease-in-out hover:text-blue-500"
              >
                <FontAwesomeIcon icon={faPlay} /> {Project.title}
              </motion.h1>
            </Modal>
          </div>
        ))}
      </motion.div>
    </section>
  );
});

export default Projects;
