import React, { useState } from 'react';
import { motion } from "framer-motion";
import ScrollLink from "react-scroll/modules/components/Link";
import Modal from 'react-modal';

import Logo from '../assets/Logo.PNG';

// Set app element for accessibility
if (typeof window !== 'undefined') {
  Modal.setAppElement('#root');
}

const DownloadModal = ({ isOpen, onCancel }) => {
  return (
    <Modal 
      isOpen={isOpen} 
      className="download-modal flex items-center justify-center"
      overlayClassName="fixed inset-0 bg-black bg-opacity-50"
    >
      <div className="modal-content bg-white p-6 rounded shadow-lg mt-24 max-w-md mx-auto">
        <p className="text-lg mb-4">Do you want to download the resume?</p>
        <div className="flex justify-end">
          <button 
            className="px-4 py-2 bg-gray-200 hover:bg-gray-300 rounded transition-colors" 
            onClick={onCancel}
          >
            Cancel
          </button>
        </div>
      </div>
    </Modal>
  );
};

const Navbar = () => {
  const [open, setOpen] = useState(false);
  const [showModal, setShowModal] = useState(false);

  const toggleMenu = () => {
    setOpen(!open);
  };

  const onCancel = () => {
    setShowModal(false);
  };

  const socialLinks = [
    {
      name: "GitHub",
      url: "https://github.com/trentbyu",
      icon: "https://github.githubassets.com/assets/GitHub-Mark-ea2971cee799.png",
      alt: "GitHub Logo"
    },
    {
      name: "LinkedIn",
      url: "https://www.linkedin.com/in/trentblack010/",
      icon: "https://t3.ftcdn.net/jpg/04/37/88/86/360_F_437888641_XrjjuAwATXWNx10jQurCDaXXJnobhDi4.jpg",
      alt: "LinkedIn Logo"
    }
  ];

  return (
    <div className="NavBar z-[9999] fixed w-full">
      <div className="shadow-md w-full fixed top-0 left-0">
        <div className="md:flex items-center justify-between bg-gray-100 py-4 md:px-10 px-7">
          {/* Logo Section */}
          <div className="font-bold text-2xl cursor-pointer flex items-center font-[Poppins] text-gray-800">
            <a href="/">
              <img 
                className="rounded-sm bg-transparent h-12" 
                src={Logo} 
                alt="Logo" 
              />
            </a>
          </div>
          
          {/* Mobile Menu Button */}
          <div 
            onClick={toggleMenu} 
            className="text-3xl absolute right-8 top-6 cursor-pointer md:hidden"
          >
            <svg 
              xmlns="http://www.w3.org/2000/svg" 
              viewBox="0 0 24 24" 
              fill="currentColor" 
              className="w-6 h-6 text-blue-900"
            >
              <path 
                fillRule="evenodd" 
                d="M3 5.25a.75.75 0 01.75-.75h16.5a.75.75 0 010 1.5H3.75A.75.75 0 013 5.25zm0 4.5A.75.75 0 013.75 9h16.5a.75.75 0 010 1.5H3.75A.75.75 0 013 9.75zm0 4.5a.75.75 0 01.75-.75h16.5a.75.75 0 010 1.5H3.75a.75.75 0 01-.75-.75zm0 4.5a.75.75 0 01.75-.75h16.5a.75.75 0 010 1.5H3.75a.75.75 0 01-.75-.75z" 
                clipRule="evenodd" 
              />
            </svg>
          </div>

          {/* Navigation Links */}
          <ul className={`md:flex md:items-center md:pb-0 pb-12 absolute md:static bg-gray-100 md:z-auto z-[-1] left-0 w-full md:w-auto md:pl-0 pl-9 transition-all duration-500 ease-in ${open ? 'top-20' : 'top-[-490px]'}`}>
            {/* Social Media Links */}
            {socialLinks.map((link) => (
              <motion.li
                key={link.name}
                whileTap={{ scale: 1.25 }}
                whileHover={{ scale: 0.85 }}
                className="md:ml-8 text-xl md:my-0 my-4 sm:my-5"
              >
                <a
                  href={link.url}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-gray-800 hover:text-gray-400 duration-500 select-none cursor-pointer flex justify-center"
                  aria-label={link.name}
                >
                  <img 
                    src={link.icon} 
                    alt={link.alt} 
                    className="sm:w-16 sm:h-16 h-10 w-10" 
                  />
                </a>
              </motion.li>
            ))}
          </ul>
        </div>
      </div>
      
      {/* Modal */}
      {showModal && <DownloadModal isOpen={showModal} onCancel={onCancel} />}
    </div>
  );
};

export default Navbar;
